import React from 'react';
import { CardData } from '../types';
import Card from './Card';

export default function GameBoard({ allCards, onPlayerClick }: { allCards: CardData[], onPlayerClick: (idx: number) => void }) {
  
  // ====================================================================
  // üéõÔ∏è CONFIGURATION G√âOM√âTRIQUE
  // ====================================================================

  // 1. POSITION DES JOUEURS (Mains)
  const DIST_NORD = 240;
  const DIST_SUD  = 240;
  const DIST_OUEST= 400;
  const DIST_EST  = 400;

  // 2. POSITION DES LABELS (Nouveau r√©glage)
  // On rapproche les textes de 80px vers le centre par rapport aux cartes
  const LABEL_OFFSET = 80; 

  // 3. CONFIGURATION DE L'√âVENTAIL
  const HAND_RADIUS = 600; 
  const ANGLE_STEP = 4;

  // Debug
  const SHOW_DEBUG_CENTER = false; 

  // ====================================================================

  const getCardStyle = (card: CardData) => {
      // --- LOGIQUE GAGN√â ---
      if (card.status === 'WON') {
          const winPos = [
              `translate(0px, 500px)`,    // Sud
              `translate(-600px, 0px)`,   // Ouest
              `translate(0px, -500px)`,   // Nord
              `translate(600px, 0px)`     // Est
          ];
          return {
              transform: `translate(-50%, -50%) ${winPos[card.winnerIndex || 0]} scale(0.4)`,
              opacity: 0,
              zIndex: card.zIndex
          };
      }

      // --- LOGIQUE TABLE ---
      if (card.status === 'TABLE') {
          const rX = (card.tableRotation * 5) % 15 - 7;
          const rY = (card.tableRotation * 7) % 15 - 7;
          return {
              transform: `translate(-50%, -50%) translate(${rX}px, ${rY}px) rotate(${card.tableRotation}deg) scale(1.1)`,
              opacity: 1,
              zIndex: card.zIndex
          };
      }

      // --- LOGIQUE MAIN (√âVENTAIL) ---
      const cardsInHand = allCards.filter(c => c.owner === card.owner && c.status === 'HAND');
      
      const colorPriority: Record<string, number> = { 'Blue': 1, 'Green': 2, 'Pink': 3, 'Yellow': 4, 'Rocket': 5 };
      cardsInHand.sort((a, b) => {
          if (a.color !== b.color) return colorPriority[a.color] - colorPriority[b.color];
          return a.value - b.value;
      });
      
      const myIndex = cardsInHand.findIndex(c => c.id === card.id);
      const total = cardsInHand.length;

      const angle = (myIndex - (total - 1) / 2) * ANGLE_STEP;
      const centerHTML = "translate(-50%, -50%)";

      let playerRotation = 0;
      let distance = 0;

      switch(card.owner) {
        case 0: // SUD
            playerRotation = 0; 
            distance = DIST_SUD; 
            break;
        case 1: // OUEST
            playerRotation = 90; 
            distance = DIST_OUEST; 
            break;
        case 2: // NORD
            playerRotation = 180; 
            distance = DIST_NORD; 
            break;
        case 3: // EST
            playerRotation = -90; 
            distance = DIST_EST; 
            break;
      }
      
      const transform = `
        ${centerHTML} 
        rotate(${playerRotation}deg) 
        translate(0px, ${distance + HAND_RADIUS}px) 
        rotate(${angle}deg) 
        translate(0px, -${HAND_RADIUS}px)
      `;

      return { transform, opacity: 1, zIndex: myIndex };
  };

  // Helper pour g√©n√©rer un bouton de joueur styl√©
  const renderPlayerLabel = (label: string, index: number, x: number, y: number, rotation: number = 0, isVertical: boolean = false) => {
      return (
        <button 
            onClick={() => onPlayerClick(index)}
            className="absolute group z-50 flex items-center justify-center outline-none"
            style={{ 
                // On place le bouton au point X, Y
                left: 0, 
                top: 0,
                // On centre le bouton sur ce point, puis on le d√©place √† la position voulue, puis on le tourne
                transform: `translate(-50%, -50%) translate(${x}px, ${y}px) rotate(${rotation}deg)`,
                writingMode: isVertical ? 'vertical-lr' : 'horizontal-tb'
            }}
        >
            <div className="flex flex-col items-center justify-center transition-transform duration-300 group-hover:scale-110">
                <span className="text-white/30 font-bold tracking-[0.2em] text-sm group-hover:text-purple-400 transition-colors whitespace-nowrap">
                    {label}
                </span>
                <span className={`text-[10px] text-gray-500 opacity-0 group-hover:opacity-100 transition-opacity flex items-center gap-1 mt-1 ${isVertical ? 'rotate-90 py-2' : ''}`}>
                    ‚úèÔ∏è √âditer
                </span>
            </div>
        </button>
      );
  };

  return (
    <div className="w-full h-full relative bg-[#2c3e50] overflow-hidden shadow-inner flex items-center justify-center">
        
        {/* LE POINT Z√âRO ABSOLU */}
        <div className="relative w-0 h-0 flex items-center justify-center">

            {/* CROIX DE DEBUG */}
            {SHOW_DEBUG_CENTER && (
                <>
                    <div className="absolute w-[200vw] h-[1px] bg-red-500/50 pointer-events-none"></div>
                    <div className="absolute h-[200vh] w-[1px] bg-red-500/50 pointer-events-none"></div>
                </>
            )}

            {/* --- BOUTONS DIRECTIONS --- */}
            {/* On utilise DIST_... - LABEL_OFFSET pour √™tre "devant" les cartes */}

            {/* NORD (Haut) : Y n√©gatif */}
            {renderPlayerLabel("NORD", 2, 0, -(DIST_NORD - LABEL_OFFSET))}

            {/* SUD (Bas) : Y positif */}
            {renderPlayerLabel("SUD", 0, 0, (DIST_SUD - LABEL_OFFSET))}
            
            {/* OUEST (Gauche) : X n√©gatif, texte vertical, tourn√© de 180 pour faire face au centre */}
            {renderPlayerLabel("OUEST", 1, -(DIST_OUEST - LABEL_OFFSET), 0, 180, true)}
            
            {/* EST (Droite) : X positif, texte vertical */}
            {renderPlayerLabel("EST", 3, (DIST_EST - LABEL_OFFSET), 0, 0, true)}


            {/* --- CARTES --- */}
            {allCards.map((card) => {
                const style = getCardStyle(card);
                const speed = card.status === 'WON' ? '1000ms' : '600ms';
                
                return (
                    <div 
                        key={card.id}
                        // AJOUT ICI : Si on clique sur une carte en main, on ouvre l'√©diteur de ce joueur
                        onClick={() => {
                            if (card.status === 'HAND') onPlayerClick(card.owner);
                        }}
                        className="absolute top-0 left-0 w-16 aspect-[2/3] transition-all ease-in-out"
                        style={{ 
                            ...style, 
                            transitionProperty: 'transform, opacity', 
                            transitionDuration: speed,
                            willChange: 'transform'
                        }}
                    >
                        <Card color={card.color} value={card.value} className="w-full h-full shadow-2xl border border-black/30 rounded-lg" />
                    </div>
                );
            })}

        </div>
      </div>
  );
}